cc.Class({
    extends: cc.Component,

    properties: {
        title:{
            default:null,
            type:cc.Sprite
        },
        closeBtn: {
            default: null,
            type: cc.Button,
        },
    },
    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.closeBtn.node.on(cc.Node.EventType.TOUCH_END,this.close,this)
    },
    binit: function(obj){
        if (obj.hasOwnProperty("size")){
            this.node.width = obj.size.width;
            this.node.height = obj.size.height;
        }
    },
    close(){
        this.node.destroy();
    }
    // update (dt) {},
});
